<template>

    <div class='SyntaxContent'>
        <h2>Java </h2>
    
        <!---------------------------------------------------------->
        <div class='sub-title first-title'>
            Java
        </div>

        <span style='margin-left:30px'>Java <i>Hello World</i></span>

        <div class = 'code-block'>
            <span> <span style="color:#FF75B5">public class </span> <span style='color:#FFB86C'>HelloWorld</span> {</span><br/>
            <br />
            <span style='margin-left:20px'><span style="color:#FF75B5">public</span>
            <span style='color:#19F9D8'> static void</span> <span style='color:#FFB86C'>main</span> ( <span style='color:#19F9D8'>String</span>
             [ ]<span style='color:#B6B6B6'>args</span> ) {</span><br/>
            <span style='margin-left:40px'>
                <span style='color:#FFB86C'>System.out.println</span>( "<span style='color:#B6B6B6'>Hello World</span>" ) ; 
                <span style='color:#67AB7D'>//  Hello World</span></span><br/>
            <span style='margin-left:20px'>}</span><br/>
            <br/>
            <span>}</span><br/>
        </div>
        
        <span style='margin-left:30px'></span><br/>

        <ul style='margin-left:30px;line-height:35px'>
            <li></li>
            <li>HelloWorld.java</li>
            <li> cmd  C:\</li>
            <li> javac HelloWorld.java </li>
            <li> java HelloWorld </li>
        </ul>

        <span style='margin-left:30px'> Hello World</span><br/>

        <div class='code-block'>
            <span><span style='color:#FF75B5'>C : > </span> <span style='color:#19F9D8'> javac</span> HelloWorld.java</span><br/>
            <span><span style='color:#FF75B5'>C : > </span> <span style='color:#19F9D8'> java</span> HelloWorld </span><br />
            <span>Hello World</span>
        </div>

        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(1)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(1)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(1)'></el-button>
        </div>
        <!---------------------------------------------------------->
        <div class='sub-title second-title'>
            
        </div>

        <span style='margin-left:30px'> Java </span><br/>
        <ul style='margin-left:30px;line-height:35px'>
            <li><b></b> </li>
            <li><b></b> </li>
            <li><b></b> </li>
            <li><b></b> </li>
        </ul>
        <el-divider></el-divider>
        <span style='margin-left:30px'> Java </span><br/>
        <ul style='margin-left:30px;line-height:35px'>
            <li><b></b> Java  Hello  hello </li>
            <li><b></b> </li>
            <li><b></b> </li>
            <li><b></b>  Java </li>
            <li><b></b>  Java  <b>public static void main(String []args) </b></li>
        </ul>

        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(2)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(2)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(2)'></el-button>
        </div>

        <!---------------------------------------------------------->
        <div class='sub-title third-title'>
            
        </div>

        <span style='margin-left:30px'>Java </span><br/>
        <span style='margin-left:30px'> Java </span><br/>
        <ul style='margin-left:30px;line-height:35px'>
            <li>A-Z  a-z,$_</li>
            <li>A-Z  a-z,$_</li>
            <li></li>
            <li></li>
            <li>age$salary_value__1_value</li>
            <li>123abc-salary</li>
        </ul>
        <el-divider></el-divider>
        <span style='margin-left:30px'>Java</span><br/>
        <ul style='margin-left:30px;line-height:35px'>
            <li> : <b>default, public , protected, private</b></li>
            <li> : <b>final, abstract, static, synchronized</b></li>
        </ul>
        <span style='margin-left:30px'> Java </span><br/>
        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(3)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(3)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(3)'></el-button>
        </div>


        <!---------------------------------------------------------->
        <div class='sub-title forth-title'>
            Java 
        </div>

        <span style='margin-left:30px'> C/C++Java  Java </span><br/>

        <div class = 'code-block'>
            <span> <span style="color:#FF75B5">public class </span> <span style='color:#FFB86C'>HelloWorld</span> {</span><br/>
            <br />
            
            <span style='margin-left:20px'><span style="color:#67AB7D">/* Java</span></span><br/>
            <span style='margin-left:24px'><span style="color:#67AB7D"> * Hello World</span></span><br/>
            <span style='margin-left:24px'><span style="color:#67AB7D"> * </span></span><br/>
            <span style='margin-left:24px'><span style="color:#67AB7D"> * /</span></span><br/>
            
            <span style='margin-left:20px'><span style="color:#FF75B5">public</span>
            <span style='color:#19F9D8'> static void</span> <span style='color:#FFB86C'>main</span> ( <span style='color:#19F9D8'>String</span>
             [ ]<span style='color:#B6B6B6'>args</span> ) {</span><br/>

            <br />
            <span style='margin-left:40px'><span style="color:#67AB7D"> // </span></span><br/>
            <span style='margin-left:40px'><span style="color:#67AB7D"> /*  */</span></span><br/>
            <br />

            <span style='margin-left:40px'>
                <span style='color:#FFB86C'>System.out.println</span>( "<span style='color:#B6B6B6'>Hello World</span>" ) ; 
                <span style='color:#67AB7D'>//  Hello World</span></span><br/>
            <span style='margin-left:20px'>}</span><br/>
            <br/>
            <span>}</span><br/>
        </div>

        <div class='mark-block'>
            <el-button type="primary" icon="el-icon-edit" circle @click='clickNote(4)'></el-button>
            <el-button type="success" icon="el-icon-check" circle @click='clickKnow(4)'></el-button>
            <el-button type="warning" icon="el-icon-star-off" circle @click='clickUnknow(4)'></el-button>
        </div>

        <!---------------------------------------------------------->
        <el-divider></el-divider>
        
        <div class='end-block'>
            
            <el-button type="danger" @click="dialogVisible = true" round></el-button>
            <el-button type="warning" @click="testVisible = true" round></el-button>
            <el-button type="success" round></el-button>
        </div>
        <!---------------------------------------------------------->

        <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="30%"
            >
            <div v-if='know.length!=0||unknow.length!=0'>
                <span v-if='know.length!=0'>
                <b>:</b> <span style='font-weight:bold;color:#67C23A;margin-right:1em' v-for="(k,index) in know" :key="index">{{k}} </span>
                </span>
                <br /><br />

                <span v-if='unknow.length!=0'>
                <b>:</b> <span style='font-weight:bold;color:#E6A23C;margin-right:1em' v-for="(u,index) in unknow" :key="index">{{u}} </span>
                </span>
            </div>

            <div v-if='know.length==0&&unknow.length==0'>
                <b>~</b>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogVisible = false"> </el-button>
            </span>
            </el-dialog>

        <!---------------------------------------------------------->
        <el-dialog
            title=""
            :visible.sync="testVisible"
            width="30%"
            :before-close="handleClose"
            >
            <el-card class="box-card">
                <div class="test-question">
                    <b>Q: </b>
                </div>
                
                <div class="test-select" style='margin:20px 0'>
                    <el-radio v-model="select" label="1" style='display:block;line-height:36px;'>default</el-radio>
                    <el-radio v-model="select" label="2" style='display:block;line-height:36px;'>123abc</el-radio>
                    <el-radio v-model="select" label="3" style='display:block;line-height:36px;'>_$112as</el-radio>
                    <el-radio v-model="select" label="4" style='display:block;line-height:36px;'>-$kiki</el-radio>
                </div>

                <div class="test-confirm" style='text-align:right'>
                    <el-button type="primary" @click="testAnswer" > </el-button>
                </div>

            </el-card>

        </el-dialog>

    </div>

</template>



<script>
export default {
     data() {
      return {
        select:-1,
        dialogVisible: false,
        testVisible: false,
        know : [],
        unknow:[],
      };
    },

    methods:{
        clickKnow(value){

            switch(value){
                case 1:$('.first-title').css('background-color','#67C23A');this.knowPush("Java");break;
                case 2:$('.second-title').css('background-color','#67C23A');this.knowPush("");break;
                case 3:$('.third-title').css('background-color','#67C23A');this.knowPush("");break;
                case 4:$('.forth-title').css('background-color','#67C23A');this.knowPush("Java ");break;
            }

            this.know = Array.from(new Set(this.know))

            this.$notify({
                title: '',
                message: '',
                type: 'success'
            });
        },

        clickUnknow(value){

            switch(value){
                case 1:$('.first-title').css('background-color','#E6A23C');this.unknowPush("Java");break;
                case 2:$('.second-title').css('background-color','#E6A23C');this.unknowPush("");break;
                case 3:$('.third-title').css('background-color','#E6A23C');this.unknowPush("");break;
                case 4:$('.forth-title').css('background-color','#E6A23C');this.unknowPush("Java ");break;
            }

            this.unknow = Array.from(new Set(this.unknow))

            this.$notify({
                title: '',
                message: '',
                type: 'warning'
            });
        },

        knowPush(value){
            this.know.push(value)
            if(this.unknow.indexOf(value)!=-1){
                this.unknow.splice(this.unknow.indexOf(value),1);
            }
        },
        unknowPush(value){
            this.unknow.push(value)
            if(this.know.indexOf(value)!=-1){
                this.know.splice(this.know.indexOf(value),1);
            }
        },
        testAnswer(){
            if(this.select==3){
                this.$message({
                    message: '',
                    type: 'success'
                });
            }else{
                 this.$message.error('');
            }
        },
        handleClose(){
            this.select = -1;
            this.testVisible = false;
        },
        clickNote(value){
            switch(value){
                case 1:this.$emit('note','Java');break;
                case 2:this.$emit('note','');break;
                case 3:this.$emit('note','');break;
                case 4:this.$emit('note','Java ');break;
            }
        }

    },
}
</script>


<style scoped>
    .SyntaxContent{
        text-align:left;
    }

    .SyntaxContent h2{
        width:fit-content;
        display: inline-block;
        padding:2px 12px 4px;
        border-bottom: 4px #FF4F4F solid;
    }
    
    .sub-title{
        margin-top:5%;
        margin-bottom: 5%;
        padding-left:2em;
        padding-right:2em;
        font-weight: bold;
        font-size:20px;
        width:fit-content;
        background-color:#FAFAFA;
        height:2em;
        line-height:2em;
        border-radius:20px;
    }

    .code-block{
        color:white;
        margin-top:20px;
        margin-left:20px;
        margin-bottom: 20px;
        min-height:100px;
        width:80%;
        background-color: #292A2B;
        box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.2);
        border-radius: 15px;
        padding:16px;
        font-size:15px;
        line-height:1.5;
        font-family: Inter, system-ui, sans-serif;
    }

    .mark-block{
        width:80%;
        margin-top:50px;
        margin-left:20px;
        margin-bottom: 20px;
        text-align: end
    }
    .end-block{
        width:80%;
        margin-top:50px;
        margin-left:20px;
        margin-bottom: 100px;
        text-align: center
    }
</style>


