<template>
    <div class="block">
        <h2>{{language=='Chinese'?'':'Pre-Course Test'}}</h2>
        <!-- <div class="divider"></div> -->

            <!-- <Card
                v-for = "(q,index) in questions"
                :key = "index"
                :class = "index"
                :question='questions[index]'
                :answers='answers[index]' 
                :correct='corrects[index]' 
                @next='handleNext'
            /> -->
            <Card
                v-if = this.isTest
                class='card'
                :percentage='percentage'
                :question='questions[this.index]'
                :answers='answers[this.index]' 
                :correct='corrects[this.index]' 
                :description='descriptions[this.index]'
                @next='handleNext'
                @result='handleResult'
            />

            <resultCard
                v-if = !this.isTest
                @check = "drawer=true"
            />
        <!---------------------------------------------------------------->
        <!-- <transition name='el-zoom-in-center'>
            <Card
                v-show='show1' 
                :question='questions[1]'
                :answers='answers[1]' 
                :correct='corrects[1]' 
                @next='handleNext'
            />
        </transition> -->
        <el-drawer
            title=""
            :visible.sync="drawer"
            :with-header="false"
            size = "40%">
            

            <div style='margin:40px'>
                <checkCard
                    v-for = "(q,index) in questions"
                    :key = "index"
                    :question='questions[index]'
                    :answers='answers[index]' 
                    :correct='corrects[index]' 
                    :myAnswers ='myAnswers[index]'
                />
                
            </div>
        </el-drawer>

</div>
</template>

<script>
import Card from './components/Card';
import resultCard from './components/resultCard';
import checkCard from './components/checkCard';

export default {
    components:{
        Card,
        resultCard,
        checkCard
    },
    data() {
      return {
        drawer:false,
        isTest:true,
        percentage:10,
        know:[],
        index:0,
        corrects:[
            0,1,0,3,3,1,2,1,1,0
        ],
        questions:[
            '1.:',
            '2.Java:',
            '3.23:',
            '4.Spring:',
            '5.:',
            `6.JSP'include'JSPHTML( )`,
            '7. JSP',
            '8.connectorJavaIO( )',
            '9.MySQL4catmore',
            '10.MongoDB'
        ],
        answers:[
            //
            ['A. Javamainclass',
             'B. Javamain',
             'C. Java',
             'D. Javamain'
            ],
            //
            ['A. ',
             'B. ',
             'C. ',
             'D. '
            ],
            //
            ['A. setLayout(new GridLayout(2, 3))',
             'B. setLayout(new GridLayout(3, 2))',
             'C. setLayout(3, 2)',
             'D. setLayout(new FlowLayout(3, 2))'
            ],
            //
            [
                'A. SpringJavaEE',
                'B.Spring',
                'C.Spring',
                'D.SpringAOP',
            ],
            //
            [
                'A. ',
                'B.',
                'C.',
                'D.',
            ],
            //
            [
                'A. ',
                'B. ',
                'C. ',
                'D. ASP',
            ],
            //
            [
                'A. Response',
                'B. Session',
                'C. Application',
                'D. Request',
            ],
            //
            [
                'A. BIO',
                'B. NIO',
                'C. APR',
                'D. None',
            ],
            //
            [
                'A. (err-log)',
                'B. (bin-log)',
                'C. (query-log)',
                'D. (slow-log)',
            ],
            //
            [
                'A. ',
                'B. ',
                'C. ',
                'D. ',
            ],
        ],
        descriptions:[

           [
            '1:',
            'A: ',
            'B: Javamain',
            'C: ()public,,public,',
            'D: main',
            ],

            [
             '2:',
              'A: ',
              'B: ',
              'C: private',
              'D: ',
            ],

            [
             '3:',
             'GridLayoutrowcolumnrowcolumn'
            ],

            [
              '4:',
              `springAOP AOPAspect Oriented Programming
              AOPOOP
              Spring SpringAOP
              log4jApache`
            ],

            [
             '5:',
             `,. 
             D`
            ],

            [
             '6:',
             `includeJSPJSPHTMLJAVA WEB
             include`
            ],

            [
             '7:',
             `ABCDJSPapplicationWeb`
            ],

            [
             '8:',
             'A: BIO: ',
             'B: NIO: JavaIO',
             'C: APR: Apache Portable Runtimeio',
             'D: ',   
            ],

            [
             '9:',
             `catmore`
            ],

            [
             '10:',
             `Mongodbtext
             `
            ]
        
        ],
        myAnswers:[],//myAnswer
      };
    },
    methods:{
        handleNext(prop,myAnswer){
            //this['show'+this.index]=false;
            this.know[this.index] = prop;
            this.myAnswers[this.index] = myAnswer;
                this.index += 1;
                this.percentage+=10;
            
            
            //this['show'+this.index]=true;
            // $('.card').slideUp("1500",()=>{
            //     this.index += 1;
            //     $('.card').slideDown("1500")
            // });
        },
        handleResult(prop,myAnswer){
            this.know[this.index] = prop;
            this.myAnswers[this.index] = myAnswer;
            console.log(this.know);
            console.log(this.myAnswers);
            this.isTest = false;
            this.$store.commit('changeKnow',this.know)
        }
    },
    mounted(){
        this.index = 0;
    },
    computed:{
        language:function(){
            return this.$store.state.language
        }
    }
  };
</script>

<style>
.block{
    text-align: left;
    margin-bottom: 200px;
}
.block h2{
        width:fit-content;
        display: inline-block;
        padding:2px 12px 4px;
        border-bottom: 4px #FF4F4F solid;
    }
.radio{
    margin-top:5%;
    margin-bottom: 5%;
    font-family: Microsoft JhengHei;
}
.divider{
        background-color: #FF4F4F;
        height: 4px;
        width: 230px;
        margin-top: -18px;
    }
.block .activity-content{
    font-size:14px;
    line-height:24px;
    margin-bottom:-20px;
}

.el-drawer__body {
    overflow: auto;
    /* overflow-x: auto; */
}

/*2.*/
.el-drawer__container ::-webkit-scrollbar{
    display: none;
}
</style>


